
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

import { GetUserInfo } from 'src/Api/api'
import {
mapActions
} from 'vuex'
import { USER_SIGNOUT } from 'src/store/user'
var img1 = require('src/assets/swiper/1.png')
var img2 = require('src/assets/swiper/2.png')
var img3 = require('src/assets/swiper/3.png')
var topbg = require('src/assets/index/top_bg.png')

const imgList = [
img1,
img2,
img3
]

export default {
components: {
Swiper,
SwiperItem,
XButton,
Box,
GroupTitle,
Group,
Flexbox,
FlexboxItem,
Divider
},
methods: {
...mapActions([USER_SIGNOUT]),
change (value) {
console.log('change:', value)
},
GoUserInfo () {
this.$router.push('/userinfo')
},
GoActivity () {
this.$router.push('/activity')
},
GetUser () {
GetUserInfo({openid: this.$store.state.user.token}).then(response => {

console.log('GetUserInfo', response.data)
this.user = response.data.data.wxUser





})
}
},
data () {
return {
user: {name: ''},
submit001: 'click me',
disable001: false,
demo04_list: imgList,
img: {top_bg: topbg}
}
},
created () {
this.GetUser()
}
}



